import React from "react";
import { withState } from "./../../Context";

const AdditionalServices = withState(
  ({
    state: {
      translations: { t }
    },
    entries
  }) => (
    <ul className="custom-list check-list">
      {Object.entries(entries).map((checkListItem, index) => {
        let [key, value] = checkListItem;

        switch (key) {
          case "balcony":
            key = t("apartmentPageBalconyLoggia");
            break;
          case "fridge":
            key = t("apartmentPageFridge");
            break;
          case "grill":
            key = t("apartmentPageGrill");
            break;
          case "oven":
            key = t("apartmentPageOven");
            break;
          case "garden":
            key = t("apartmentPageGarden");
            break;
          case "seaView":
            key = t("apartmentPageSeaView");
            break;
          case "parking":
            key = t("apartmentPageParking");
            break;
          case "internetConnection":
            key = t("apartmentPageInternetConnection");
            break;
          case "radioSateliteTv":
            key = t("apartmentPageRadioSateliteTV");
            break;
          case "separateToilet":
            key = t("apartmentPageSeparateToilet");
            break;
          case "childrenFriendly":
            key = t("apartmentPageChildrenFriendly");
            break;
          default:
            return "";
        }

        return (
          <li key={index} className={!value ? "unchecked" : ""}>
            {key}
          </li>
        );
      })}
    </ul>
  )
);

export default AdditionalServices;
