import React from "react";
import { withState } from "./../../Context";

const AdditionalInformation = withState(
  ({
    state: {
      translations: { t },
      additionalInformation
    }
  }) => (
    <div className="toggle-container property-accomodation">
      <h5 className="toggle-title">{t("apartmentPageAdditionalInfo")}</h5>
      <div className="toggle-content">
        <ul className="custom-list">
          {Object.entries(additionalInformation).map((entry, index) => {
            let [key, value] = entry;

            switch (key) {
              case "seaDistance":
                key = t("apartmentPageAdditionalInfoDistanceFromSea", {
                  value
                });
                break;
              case "nearestShop":
                key = t("apartmentPageAdditionalInfoNearestShop", { value });
                break;
              case "restaurantDistance":
                key = t("apartmentPageAdditionalInfoDistanceFromRestaurant", {
                  value
                });
                break;
              case "postOfficeDistance":
                key = t("apartmentPageAdditionalInfoPostOffice", { value });
                break;
              default:
                return "";
            }

            return (
              <li key={index} dangerouslySetInnerHTML={{ __html: key }}></li>
            );
          })}
        </ul>
      </div>
    </div>
  )
);

export default AdditionalInformation;
