import React from "react";
import { withState } from "./../../Context";

const Description = withState(({ state: { translations: { t } }, entry }) => (
  <>
    <div className="description-text">
      <h4>{t("apartmentPageObjectDescriptionTitle")}</h4>
      <br />
      {entry && (
        <p
          dangerouslySetInnerHTML={{
            __html: entry
          }}
        ></p>
      )}
    </div>
    <hr className="my-4" style={{ marginRight: "-15px" }} />
  </>
));

export default Description;
