import React from "react";
import { Row, Col } from "react-bootstrap";
import PageWrapper from "./../components/page-wrapper";
import { withState } from "./../Context";
import {
  Accomodation,
  AdditionalInformation,
  AdditionalServices,
  Address,
  BookForm,
  Description,
  ObjectRating,
  Prices,
  PropertyImages,
  PropertyMap
} from "./../components/apartments";

import "./apartment.scss";

const ApartmentPage = withState(
  ({
    match: {
      params: { apartmentNumber }
    },
    state: {
      translations: { t, i18n },
      apartments
    }
  }) => {
    const {
      prices,
      details: { additionalServices },
      title,
      accomodation,
      rating,
      description,
      images
    } = apartments[apartmentNumber];

    return (
      apartments[apartmentNumber] && (
        <PageWrapper>
          <div className="main-wrapper">
            <div className="sidebar">
              <aside className="property-location-details">
                {title && (
                  <h3 className="property-location-title d-none d-sm-none d-md-none d-lg-block d-xl-block">
                    {title}
                  </h3>
                )}
                <PropertyMap />
                <div className="property-info">
                  <h4 className="property-info-title">
                    {t("apartmentPageDetails")}
                  </h4>

                  <AdditionalInformation />
                  {additionalServices && (
                    <AdditionalServices entries={additionalServices} />
                  )}
                  <Address />
                </div>
              </aside>
            </div>
            <div className="content">
              <div className="property-details">
                {title && (
                  <h3 className="property-location-title d-block d-sm-block d-md-block d-lg-none d-xl-none">
                    {title}
                  </h3>
                )}
                <div className="property-images">
                  {images && <PropertyImages images={images} />}
                </div>

                <Row>
                  <Col sm={12} lg={8}>
                    <div className="property-description">
                      {description && (
                        <Description entry={description[i18n.language]} />
                      )}
                      {accomodation && <Accomodation entries={accomodation} />}
                    </div>
                  </Col>
                  <Col sm={12} lg={4}>
                    <div className="property-panel">
                      {prices && <Prices prices={prices} />}
                      {rating && <ObjectRating rating={rating} />}
                      <BookForm />
                    </div>
                  </Col>
                </Row>
              </div>
            </div>
          </div>
        </PageWrapper>
      )
    );
  }
);

export default ApartmentPage;
