import React from "react";
import { withState } from "./../../Context";
import * as accomodationIcons from "./../../img/accomodation-icons";

const Accomodation = withState(({ state: { translations: { t } }, entries }) =>
  Object.entries(entries).map((accomodation, index) => {
    let [key, value] = accomodation;

    let img;
    switch (key) {
      case "floor":
        key = t("apartmentPageFloorNumber");
        img = accomodationIcons.Steps;
        break;
      case "bedrooms":
        key = t("apartmentPageNumberOfBedrooms");
        img = accomodationIcons.Bed;
        break;
      case "bathrooms":
        key = t("apartmentPageNumberOfBathrooms");
        img = accomodationIcons.BathTub;
        break;
      case "bbq":
        key = t("apartmentPageYouCanUseBBQ");
        img = accomodationIcons.BBQ;
        break;
      case "wifi":
        key = t("apartmentPageWiFiAvailable");
        img = accomodationIcons.WiFi;
        break;
      case "parkings":
        key = t("apartmentPageParkingSpots");
        img = accomodationIcons.ParkingSpot;
        break;
      case "aircon":
        key = t("apartmentPageAirconAvailable");
        img = accomodationIcons.Aircon;
        break;
      case "washingMachine":
        key = t("apartmentPageWashingMachine");
        img = accomodationIcons.Washer;
        break;
      default:
        return "";
    }

    return (
      <div
        key={index}
        className="d-flex align-items-center my-3 ml-2"
        style={{ gap: "20px" }}
      >
        <img src={img} alt="" style={{ height: "27px" }} />

        <h5 className="mb-0 mt-2">{`${value === true ? "" : value} ${key}`}</h5>
      </div>
    );
  })
);

export default Accomodation;
