import React from "react";

const Prices = ({ prices }) => (
  <div className="panel-item object-price">
    <h4 className="panel-item-title">prices_title</h4>
    <div className="price">
      {Object.entries(prices).map((price, index) => (
        <p key={index} className="price mt-1 mb-0">
          <span>{price[1].title}</span>
          <strong className="ml-1 ml-md-0">{price[1].price}</strong>
        </p>
      ))}
    </div>
  </div>
);

export default Prices;
